

















































































































import Vue from 'vue';
import QuizEventApi from '@/apis/QuizEventApi';
import {
  QuizEventModelv4, State,
} from '@/apis/models/QuizEventModel';
import Auth from '@/libs/auth';
import BackButton from '@/components/BackButton.vue';

export default Vue.extend({
  components: {
    BackButton,
  },
  data() {
    return {
      quizEvent: null as QuizEventModelv4 | null, // 本[測驗]的資訊
      State,
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    quizEventId(): string {
      return this.$store.state.currentQuizEvent.uuid;
    },
    latestState(): State {
      let latestState = State.ERROR;
      if (this.quizEvent && this.quizEvent.stateHistory) {
        const index = this.quizEvent.stateHistory.length - 1;
        latestState = this.quizEvent.stateHistory[index].state;
        return latestState;
      }
      return latestState;
    },
    memberGroupsLength(): number {
      if (this.quizEvent && this.quizEvent.memberGroups) {
        return this.quizEvent.memberGroups.length;
      }
      return 0;
    },
    quizGroupsLength(): number {
      if (this.quizEvent && this.quizEvent.quizGroups) {
        return this.quizEvent.quizGroups.length;
      }
      return 0;
    },
  },
  async mounted() {
    Auth.loginCheck();
    this.$store.commit('updateLoading', true);
    try {
      this.quizEvent = await QuizEventApi.getQuizEvent(this.quizEventId);
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  methods: {
    toEventMaker() {
      this.$router.push('/manager/quizevent-maker');
    },
    changeFormat(dateTime: string) {
      const timestamp = Date.parse(dateTime);
      const result = this.toDateStringFormat(timestamp / 1000);
      return result;
    },
    // 工具-------------------------
    toDateStringFormat(timestamp: number) { // [公式]把時間戳換成字串
      const date = new Date(timestamp * 1000);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const min = date.getMinutes();

      let addzeromonth = '';
      let addzeroday = '';
      let addzerohour = '';
      let addzeromin = '';

      if (month < 10) {
        addzeromonth = `0${month}`;
      } else {
        addzeromonth = `${month}`;
      }
      if (day < 10) {
        addzeroday = `0${day}`;
      } else {
        addzeroday = `${day}`;
      }
      if (hour < 10) {
        addzerohour = `0${hour}`;
      } else {
        addzerohour = `${hour}`;
      }
      if (min < 10) {
        addzeromin = `0${min}`;
      } else {
        addzeromin = `${min}`;
      }
      const currentDateTime = `${String(year)}-${addzeromonth}-${addzeroday} ${addzerohour}:${addzeromin}`;
      return currentDateTime;
    },
  },
});
